import { createApp } from 'vue'
import App from './App.vue'

import BootstrapVue3 from 'bootstrap-vue-3'
// Подключаем bootstrap grid сетку
import 'bootstrap/dist/css/bootstrap-grid.min.css'

// Стили Normalaiz.css
import './normalaiz.css'


// Ленивая подгрузка картинок
import { Lazyload } from 'vant';
// Модульние окна
import { Popup } from 'vant';
import { Button } from 'vant';
import { Locale } from 'vant';
import { Checkbox, CheckboxGroup } from 'vant';
import { RadioGroup, Radio } from 'vant';
import { Form, Field, CellGroup } from 'vant';
import { Collapse, CollapseItem } from 'vant';
import { Toast } from 'vant';
import { Cell} from 'vant';

import 'vant/lib/index.css';
import enUS from 'vant/es/locale/lang/en-US';
import router from './router'
import { createI18n } from 'vue-i18n'



import ua from './langs/ua.json'
import ru from './langs/ru.json'
// Хранения текущегго выбранного языка в локальном хранилище
let localStorageLang = localStorage.getItem(`lang`);

const i18n = createI18n({
  legacy: false,
  fallbackLocale: `ua`,
  // Если в локальном хранилище не буддет языка = то будет устанавливаться украинский язык
  locale: localStorageLang || `ua`,
  messages: {
    ua,
    ru
  }

})





Locale.use('en-US', enUS); // Меняю язык на английский, по умолчанию стоит - китайский -_-
let app = createApp(App).use(router)
app.use(i18n)
app.use(BootstrapVue3)
app.use(Lazyload);
app.use(Popup);
app.use(Button);
app.use(Checkbox);
app.use(CheckboxGroup);
app.use(Cell);
app.use(Collapse);
app.use(CollapseItem);
app.use(Form);
app.use(Field);
app.use(CellGroup);
app.use(Radio);
app.use(RadioGroup);
app.use(Toast);
app.mount('#app')

